.salesBox{
    background: rgba(67, 174, 170, 0.1);
    padding: 40px 70px !important;
    text-align: center
}
.salesBox h2{
    font-family: 'Poppins';
font-style: normal;
font-weight: 700;
line-height: 42px;
font-size: 32px;
text-align: center;
color: #464646;
}

.salesBox h4{
    font-family: 'Inter';
font-style: normal;
font-weight: 600;
line-height: 25px;
color: #43AEAA;

}
.salesIcons{
    width: 64px !important;
    height: auto !important;
    margin-bottom: 8px;
}
.tabletLayout{
    flex-wrap: wrap;
}

@media screen and (max-width: 900px) {
    .salesBox{
        padding: 30px 20px !important
    }
    .salesBox h2{
    line-height: 36px;
    font-size: 26px;
    }
    .salesIcons{
        width: 54px !important;
    }
    .textLayout{
        font-size: 14px !important;
    }
    
  }