.container{
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-image: linear-gradient(90.3deg, rgba(0, 0, 0, 0.3) 0.24%, rgba(67, 174, 170, 0.237) 98.28%), linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.6) 100%), url(/static/media/businesspeople-meeting-office-working.79a364cd.jpg);
}
.header{
    display: grid;
    position: fixed;
    width: 100%;
    grid-template-columns: minmax(100px, 250px) minmax(200px, 1fr) minmax(200px, 300px);
    align-items: center;
    height: 60px;
    z-index: 40;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    transition: -webkit-transform 0.6s ease;
    transition: transform 0.6s ease;
    transition: transform 0.6s ease, -webkit-transform 0.6s ease;
      
}
.header2 {
    display: grid;
    position: fixed;
    width: 100%;
    grid-template-columns: minmax(100px, 250px) minmax(200px, 1fr) minmax(200px, 300px);
    align-items: center;
    height: 60px;
    z-index: 4;
    background: rgb(255 255 255 / 81%);
    -webkit-transform: translateY(10);
            transform: translateY(10);
    transition: -webkit-transform 6s ease;
    transition: transform 6s ease;
    transition: transform 6s ease, -webkit-transform 6s ease;
    -webkit-animation: myanimation 3s;
            animation: myanimation 3s;
    color: teal !important;
  }
  @-webkit-keyframes myanimation {
    0% {
      background-color: transparent;
    }
    35% {
      background-color: linear-gradient(90deg, rgba(0, 0, 0, 0.88) 0%, rgba(5, 91, 87, 0.99) 125.98%, rgba(8, 140, 135, 0.88) 195.07%);
    }
    100% {
      background-color: linear-gradient(90deg, rgba(0, 0, 0, 0.88) 0%, rgba(5, 91, 87, 0.88) 125.98%, rgba(8, 140, 135, 0.88) 195.07%);
    }
  }
  @keyframes myanimation {
    0% {
      background-color: transparent;
    }
    35% {
      background-color: linear-gradient(90deg, rgba(0, 0, 0, 0.88) 0%, rgba(5, 91, 87, 0.99) 125.98%, rgba(8, 140, 135, 0.88) 195.07%);
    }
    100% {
      background-color: linear-gradient(90deg, rgba(0, 0, 0, 0.88) 0%, rgba(5, 91, 87, 0.88) 125.98%, rgba(8, 140, 135, 0.88) 195.07%);
    }
  }
  

