.boxLayout:hover{
    background-color: #43AEAA !important;
    fill: #ffff !important;
    stroke: #ffff  !important;
    transition: ease-in-out 0.5s;
}

.boxLayout svg{
    transform: scale(0.5);
}

.boxLayout:hover path{
    fill: #ffff !important;
    stroke: #ffff  !important;
}

.boxLayout:hover > .heading{
    color: white !important;
    fill: #ffff !important;
    stroke: #ffff  !important;
}
.boxLayout:hover > .des{
    fill: #ffff !important;
    stroke: #ffff  !important;
    color: rgba(255, 255, 255, 0.75) !important;
}
.mainContent{
    width: 100%;
    background-color: #ECF7F7;
    fill: #ffff !important;
    stroke: #ffff  !important;
    
    

}
