.advantageContainer{
position: relative;
}
.advantageRow{
    display:flex;
    padding: 2rem 1rem;
}
.left{
    display: flex;
    flex: 0.4;
    flex-direction: column;
    align-items: flex-start;
}
.left__title{
   max-width: 140px; 
}
.left__text{
font-weight: 600;
line-height: 36px;
color: #9D9797;
}
.left__heading {
    font-family: Poppins;
    font-weight: 700;
    font-size: 1.5rem;
    color: #464646;
    margin-top: 1rem;
}
.left__text span{
    color: var(--primary);
}
.right{
    flex:0.6;
}
.laptopImage{
    width: 90%;
    min-height: 60vh;
    height: 100%;
}
.cardContainer{
    display: flex;
    position: absolute;
    top: 195px;
    left: 16px;
    gap: 3.5rem;
}

@media screen and (max-width: 900px) {
    .left__heading {
        font-size: 1rem;
    }
    .left__title{
        max-width: 100px; 
     }
     .left__text{
        line-height: 24px;
        font-size: 1.01rem;

        }
        .laptopImage{
            width: 90%;
            min-height: 50vh;
            height: 100%;
        }
        .cardContainer{
            position: absolute;
            gap: 1.8rem;
        }
  }